.chat-wrapper {
    width: 40rem;
    height: 64rem;
    background: #2f3542;
    border-radius: 6px;
    padding: 22px;
    display: flex;
    flex-direction: column;
  }
  
  .input-wrapper {
    display: flex;
    height: 3.5rem;
    background: #4e525e;
    border-radius: 6px;
  }
  
  .input-wrapper input {
    flex: 1;
    border: 0;
    background: transparent;
    padding: 0 1.5rem;
    font-size: 1.1rem;
    color: #b9b9b9;
    outline: none;
  }
  
  .messages {
    display: flex;
    flex-direction: column;
    list-style: none;
    padding: 0;
    margin: 0;
    flex: 1;
    width: 100%;
    background-color: #2f3542;
    background-image: url("data:image/svg+xml,%3Csvg width='80' height='80' viewBox='0 0 80 80' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23454b58' fill-opacity='0.4'%3E%3Cpath d='M50 50c0-5.523 4.477-10 10-10s10 4.477 10 10-4.477 10-10 10c0 5.523-4.477 10-10 10s-10-4.477-10-10 4.477-10 10-10zM10 10c0-5.523 4.477-10 10-10s10 4.477 10 10-4.477 10-10 10c0 5.523-4.477 10-10 10S0 25.523 0 20s4.477-10 10-10zm10 8c4.418 0 8-3.582 8-8s-3.582-8-8-8-8 3.582-8 8 3.582 8 8 8zm40 40c4.418 0 8-3.582 8-8s-3.582-8-8-8-8 3.582-8 8 3.582 8 8 8z' /%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
  }
  
  .message-wrapper {
    border-radius: 10px;
    padding: 0.75rem 1.5rem;
    font-size: 0.85rem;
    margin: 0.5rem 0;
  }
  
  .message-content {
    margin-right: 1rem;
    font-weight: 600;
  }
  
  .message-op {
    align-self: flex-end;
    background: #4b7bec;
    color: #fff;
  }
  
  .message-remote {
    align-self: flex-start;
    background: #6c6c6c;
    color: #fff;
  }