.Shop {
    width: 40rem;
    background: #fff;
    padding: 1rem;
    border-radius: 4px;
  }
  
  ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  
  li {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    min-height: 3rem;
    margin: 0.5rem 0;
    padding: 0 0.75rem;
    border-radius: 4px; 
    font-weight: bold;
    font-size: 0.85rem;
  }
  
  .Products li {
    background: hsla(256, 86%, 43%, 0.1);
    color: hsl(256, 86%, 43%);
  }
  
  .CartItems li {
    background: hsla(0, 0%, 27%, 0.1);
    color: hsl(0, 0%, 27%);
  }
  
  button {
    border: 0;
    padding: 0.5rem 1rem;
    border-radius: 4px;
    font-weight: bold;
    cursor: pointer;
  }
  
  button:disabled {
    background: lightgray;
    cursor: default;
  }
  
  .AddToCart {
    background: hsl(256, 86%, 43%);
    color: #fff;
  }
  
  .RemoveFromCart {
    background: hsl(345, 86%, 43%);
    color: #fff;
  }